<template>
  <breadcrumb-list :items="breadcrumbs">
    <router-link
      :to="{ name: 'admin.app.create' }"
      class="font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
    >
      <i class="fa fa-plus fa-sm fa-fw"></i>
      {{ $t("labels.add") }}
    </router-link>
  </breadcrumb-list>

  <data-table :titles="titles" :collection="users">
    <template #filters>
      <div class="py-3">
        <div class="grid grid-cols-1">
          <div class="block">
            <label for="textSearch">{{ $t("attributes.name") }}</label>
            <div class="w-full mt-1.5">
              <input
                v-model="users.filters.search"
                id="textSearch"
                type="text"
                :placeholder="$t('placeholder.search')"
                class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              />
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #body>
      <tr v-for="user in users" :key="user.id">
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ user.id }}
        </td>
        <td>{{ user.fullName }}</td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <span>{{ user.username }}</span>
        </td>
        <td>
          {{ user?.role?.name }}
        </td>
        <td>
          {{ user?.hospital?.name }}
        </td>
        <td>
          {{ user?.clinic?.name }}
        </td>
        <td>
          <span
            v-if="user.deleted_at"
            key="checkUserDeleted"
            class="p-1 text-xs text-white badge bg-error"
          >
            {{ $t("labels.deleted") }}
          </span>
        </td>
        <td>{{ user.phone }}</td>
        <td>
          <div
            v-if="!user.deleted_at"
            key="checkUserNotDeleted"
            class="flex space-x-2"
          >
            <router-link
              :to="{ name: 'admin.app.edit', params: { id: user.id } }"
              :title="$t('labels.edit')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
            >
              <v-icon name="pencil-square" size="5"></v-icon>
            </router-link>

            <v-button
              color="error"
              icon
              :size="10"
              :title="$t('labels.delete')"
              @click="deleteModal(user)"
            >
              <v-icon name="archive-delete" size="5"></v-icon>
            </v-button>
          </div>
        </td>
      </tr>
    </template>
  </data-table>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="error" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import UserCollection from "@/collections/UserCollection";
import ModelRequest from "@/request/ModelRequest";

export default {
  name: "UserList",
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.users"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        {
          column: "name",
          title: this.$t("attributes.full_name"),
          sortable: true,
        },
        {
          column: "username",
          title: this.$t("attributes.username"),
          sortable: true,
        },
        {
          column: "role",
          title: this.$t("attributes.role"),
        },
        {
          column: "role",
          title: this.$t("attributes.hospital"),
        },
        {
          column: "role",
          title: this.$t("attributes.clinic"),
        },
        { title: this.$t("attributes.status") },
        {
          column: "phone",
          title: this.$t("attributes.phone"),
        },
        { title: this.$t("labels.actions") },
      ],
      users: new UserCollection(),
      deleteSelect: null,
    };
  },
  created() {
    this.users.fetchWithFilters();
  },
  methods: {
    deleteModal(model) {
      this.deleteSelect = model;
      this.$refs.modal.toggle();
    },
    deleteData() {
      const request = new ModelRequest(this.deleteSelect);

      request.delete().then(() => {
        this.$refs.modal.toggle();

        this.users.fetchWithFilters();
      });
    },
  },
};
</script>
